import MessageTop from '../../components/messageTip/index.vue'
export default {
	components: {
		MessageTop
	},
	data() {
		return {
			isShowMessage: false,
			isMessage: '反馈成功'
		}
	},
	methods: {
		hideMessage () {
			this.isShowMessage = false
		},
		submit () {
			this.isShowMessage = true;
			setTimeout(() => {
				this.isShowMessage = false;
				uni.navigateBack()
			}, 500)
		}
	}
}
